.App {
  color-scheme: dark !important;
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  /* Use roboto font */
  font-family: 'Roboto', sans-serif;
}

body {
  margin: 0%;
  padding: 0%;
  background-color: #121212;
  color: white;
}

h1 {
  font-size: 50px;
  text-align: center;
}

h2 {
  font-size: 25px;
  margin-top: 0px;
  text-align: center;
}

.header {
  flex: 30%;
  background-color: #121212;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header .inputContainer {
  display: flex;
  flex-direction: column;
}

.header input {
  width: 200px;
  height: 40px;
  border: none;
  border-radius: 10px;
  padding-left: 10px;
  font-size: 17px;
  border: 1px solid grey;
  font-weight: bold;
  transition: all 0.2s ease-in-out;
  background-color: black;
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 10px;

}

.header input:focus {
    outline: none;
    border-radius: 15px;
    border-color: #03DAC5;
  }

.header button {
  width: 80px;
  height: 105px;
  font-size: 35px;
  font-weight: bold;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.2),
    0px 2px 10px 0px rgba(0, 0, 0, 0.096);
  background-color: black;
  color: rgba(255, 255, 255, 0.7);
  margin-left: 10px;
  border: 2px solid #bb86fc;
  border-radius: 10px;
};

.header button:active {
  transform: scale(0.95);
}

.shoppingList {
  flex: 70%;
  width: 100%;
  display: flex;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 50px;
  flex-direction: column;
  background-color: #121212
}

.item {
  width: 300px;
  height: 50px;
  display: flex;
  color: white;
  margin: 15px;
}

.item .content {
  flex: 80%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.item .content span {
  display: grid;
  place-items: center;
  border: 2px solid rgba(255, 255, 255, 0.103);
  width: 100%;
  height: 100%;
  font-size: 18px;
  box-shadow: 0px 1px 2px 0px rgb(0, 0, 0),
  0px 1px 5px 0px rgb(0, 0, 0);
  background-color: #121212;
}

.item button {
  flex: 5%;
  height: 100%;
  border: 2px solid #fc868600;
  background-color: #121212;
  box-shadow: 0px 2px 5px 0px black,
    0px 2px 10px 0px black;
  margin-left: 10px;
  border-radius: 10px;
  font-weight: bolder;
}

.quantity {
  flex: 20%;
  height: 100%;
  display: flex;
}